header {
	position: fixed;
	top: 0;
	left: 0;
	padding: 0 1rem;
}

body {
	height: 100%;
	overflow: hidden;
}


.scribe {
	$self: &;

	&-controls {
		position: fixed;
		bottom: 1rem;
		right: 1rem;
		z-index: 99;
	}

	&-form {
		position: relative;
		display: flex;
		align-items: center;
		flex-direction: column;
		width: 70%;
		height: 100%;
		min-height: 100vh;
		margin: 0 auto;

		&-loaded {

			#{$self}-question {
				transition: transform 600ms ease 0s, opacity 300ms ease 0s;
				will-change: transform, opacity;
			}
		}
	}

	&-question {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;
		height: 100%;
		min-height: 100vh;
		margin: 0;
		padding: 0;
		border: none;

		&:not(:first-child) {
			opacity: 0;
		}
	}

	&-active {
		z-index: 99;
	}
}
